<template>
  <el-pagination
    :current-page="page"
    @size-change="handleSizeChange"
    @current-change="handleSizeChange"
    @prev-click="handleSizeChange"
    @next-click="handleSizeChange"
    :page-size="6"
    :background="true"
    backgroud-color="#2c3e50"
    layout="total, prev, pager, next"
    :total="count"
  ></el-pagination>
</template>
<script>
export default {
  name: "pagination",
  props: {
    count: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      page: parseInt(this.$route.params.page)
    };
  },
  methods: {
    handleSizeChange(val) {
      if(val === 1){
        this.$router.push("/")
      }else{
        this.$router.push(`/page/${val}`);
      }
      this.$emit("getData");
    }
  }
};
</script>



